:root {
  --Soft-blue: #8bacda;
  --Cyan: #00fff7;
  --Cyan-see-through: #00fff780;
  --Very-dark-blue-main: #0d192b;
  --Very-dark-blue-card: #14253d;
  --Very-dark-blue-line: #2f415b;
  --White: #fff;
}

html {
  height: 100%;
}

.container {
  width: 100%;
  height: 100%;
  background-color: var(--Very-dark-blue-main);
  justify-content: center;
  align-items: center;
  margin: 0;
  display: flex;
}

.nft-preview-card-component p, .nft-preview-card-component h1, .nft-preview-card-component a {
  color: inherit;
  margin: 0;
  padding: 0;
  text-decoration: none;
}

.nft-preview-card-component {
  box-sizing: border-box;
  width: 350px;
  height: 596px;
  background-color: var(--Very-dark-blue-card);
  border-radius: 15px;
  padding: 24px;
  font-family: Outfit;
  font-style: normal;
  box-shadow: 0 25px 50px #00000018;
}

.nft-preview-card-component a {
  cursor: pointer;
}

.nft-preview-card-component a:active {
  color: var(--Cyan) !important;
}

.nft-preview-card-component > .img-overlay-container {
  width: 302px;
  height: 302px;
  border-radius: 8px;
  display: block;
  position: relative;
  overflow: hidden;
}

.nft-preview-card-component > .img-overlay-container > img {
  width: 100%;
  height: 100%;
  position: relative;
}

.nft-preview-card-component > .img-overlay-container > .img-overlay {
  width: 100%;
  height: 100%;
  object-fit: none;
  background-color: var(--Cyan-see-through);
  margin: 0;
  padding: 0;
  display: block;
  position: absolute;
  top: 0;
  left: 0;
}

.nft-preview-card-component > .img-overlay-container:not(:active) > .img-overlay {
  display: none;
}

.nft-preview-card-component > h1 {
  color: var(--White);
  margin-top: 24px;
  margin-bottom: 16px;
  font-size: 22px;
  font-weight: 600;
  line-height: 28px;
}

.nft-preview-card-component > p {
  color: var(--Soft-blue);
  margin-bottom: 24px;
  font-size: 18px;
  font-weight: 300;
  line-height: 26px;
}

.nft-preview-card-component > .infos {
  height: 20px;
  justify-content: space-between;
  margin-bottom: 24px;
  font-size: 16px;
  line-height: 20px;
  display: flex;
}

.nft-preview-card-component > .infos > div {
  align-items: center;
  display: flex;
}

.nft-preview-card-component > .infos > .price img {
  width: 11px;
  height: 18px;
  margin-right: 6.48px;
}

.nft-preview-card-component > .infos > .price {
  color: var(--Cyan);
  font-weight: 600;
}

.nft-preview-card-component > .infos > .due img {
  width: 16px;
  height: 16px;
  margin-right: 7.7px;
}

.nft-preview-card-component > .infos > .due {
  color: var(--Soft-blue);
  font-weight: 400;
  position: relative;
  left: -2px;
}

.nft-preview-card-component > .author {
  color: var(--Soft-blue);
  border-top: 1px solid var(--Very-dark-blue-line);
  align-items: flex-start;
  padding-top: 16px;
  font-size: 16px;
  font-weight: 400;
  line-height: 20px;
  display: flex;
}

.nft-preview-card-component > .author img {
  box-sizing: border-box;
  width: 33px;
  height: 33px;
  border: 1px solid var(--White);
  border-radius: 50%;
  margin-right: 16px;
}

.nft-preview-card-component > .author > p {
  padding-top: 6px;
}

.nft-preview-card-component > .author .name {
  color: var(--White);
  margin-left: 5px;
}

